.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    transition: visibility .3s, opacity .3s linear;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
}



